.externallinks {
  background-color: $highlight-dark;
  h1,
  h2 {
    color: #fff;
  }
  .projects-row {
    color: $dark-grey;
    text-align: center;
    .project-col {
      margin-bottom: 2rem;

      .project-container {
        border: 1px solid $highlight-dark;
        border-radius: 5px;
        color: #e7e7e7;
        font-weight: bold;

        img {
          border-radius: 5px;
          transition: all 1s ease-in-out;
        }

        &:hover {
          text-decoration: none;
          color: #ffffff;
          cursor: pointer;
          img {
            transform: scale(1.05);
          }
        }
      }
    }
  }
}
